import React, {useState} from 'react';

const AddCommentForm =({articleName, setArticleInfo}) => {
const [username, setUsername] = useState('');
const [commentText, setCommentText] = useState('');

const addComment = async() => {
    const result = await fetch (`/api/articles/${articleName}/add-comment`,{
        method : 'post',
        body: JSON.stringify ({username, text: commentText }), // convert this JSON into string so server can parse.
        headers:{
            'Content-Type' : 'application/json',
        }  
    });
    const body = await result.json();  //once we get the respose, resust will hold updated version of our articleinfo which we will update on UI
    setArticleInfo(body);
    setUsername('');   //Field get reset after comment get posted
    setCommentText('');
}
return(

<div id="add-comment-form">
    <h3>Add a Comment</h3>
    <label>
        Name: <input type = "text" value={username} onChange={(event) => setUsername(event.target.value)} />
    </label>
    <label> 
        Comment: <textarea rows ="4" cols ="50" value= {commentText} onChange=  {(event)=> setCommentText(event.target.value)}/>
    </label>

<button onClick={() => addComment()}> Add Comment</button>
</div>

)};

export default AddCommentForm;